<template>
        <GmapMap 
      v-bind:center="center"
      v-bind:zoom="7"
      style="width: 100%; height: 500px"
      >
       <GmapMarker
      v-bind:key="index"
      v-for="(m, index) in markers"
      v-bind:position="m.position"
      :title="m.title"
      v-bind:clickable="true"
      v-bind:draggable="false"
      @click="profilePath(m.title)"
      />
      </GmapMap>

</template>

<script>
import { API_KEY } from "./API_KEY";
import {Loader, LoaderOptions} from 'google-maps';
import Node from '../services/NodeData'
const loader = new Loader(API_KEY);
export default {
     data(){
      return {
        center: { lat: 32, lng: -7},
        markers: [
          {
            position: { lat: '', lng: ''},
            title: ''
          }
        ]
      }
    },
    methods:  {
        profilePath (id) {
           window.location.href =  'http://localhost/#/dashboard?id=' + id
    },
    },
  mounted() {
 
  },
  created(){
    Node.getNodes()
    .then((res) => {   
      for(var i = 0; i < res.length; i++){
      this.markers.push({position: {lat: parseFloat(res[i].latitude),lng: parseFloat(res[i].longitude)} ,title: res[i].NodeId})
      }
    })
  }
}
</script>
