import Api from '@/services/Api'
import axios from 'axios'
export default {
  addnewone (Node) {
    return new Promise((resolve, reject) => {
      console.log(Node)
      Api()
        .post('/Home/addnewone', Node)
        .then(res => {
          console.log(res.data)
          resolve('Already Exist!')
        })
        .catch(err => reject(err))
    })
  },
  getNodes () {
    return new Promise((resolve, reject) => {
      Api()
        .get('/Home/getNodes')
        .then(res => {
          resolve(res.data)
        })
        .catch(err => reject(err))
    })
  },
  getnodedata (id) {
    return new Promise((resolve, reject) => {
      Api()
        .get(`/Home/getnodeData/${id}`)
        .then(data => {
          resolve(data.data.body)
        })
        .catch(err => reject(err))
    })
  },
  getNodeDetails(id){
    return new Promise((resolve, reject) =>{
      Api().get(`/Home/getNodedetails/${id}`)
      .then((res) =>{
          resolve(res.data)
      })
      .catch(err => reject(err))
    })
  },
  getWeatherData(lat, long){
    return new Promise((resolve, reject) =>{
      Api().get(`/Home/getWeatherData/${lat}/${long}`)
      .then((data)=>{
        console.log(data.data)
        resolve(data.data)
      })
      .catch(err => reject(err))
    })
  },
  export_data (data) {
    return new Promise((resolve, reject) => {
      Api()
        .post('/Home/getnodeData/', {data: data})
        .then(data => {
          resolve(data)
        })
        .catch(err => reject(err))
    })
  },
  download (id) {
    return new Promise((resolve, reject) => {
      Api()
        .get(`/Home/nodefile/${id}`)
        .then((res) => {
          resolve(res)
        })
        .catch(err => reject(err))
    })
  },
}
