import Api from '@/services/Api'
import axios from 'axios'
export default {
  login (pass, email) {
    return new Promise((resolve, reject) => {
      console.log(email)
      Api()
        .post('/Home/Login', {email: email, password: pass})
        .then(res => {
          localStorage.setItem('authToken', res.data)
          resolve(res)
        })
        .catch(err => reject(err))
    })
  },
  AddAdmin (pass, email) {
    return new Promise((resolve, reject) => {
      console.log(email)
      Api()
        .post('/Home/Creat', {email: email, password: pass})
        .then(res => {
          console.log(res)
          resolve('Already Exist!')
        })
        .catch(err => reject(err))
    })
  },
}